<template>
<v-col cols="12" sm="6">
  <v-card>
    <v-card-title>Платежи клиента</v-card-title>
    <v-card-text style="white-space: pre-wrap;">
      <div>{{payments && payments.length}} платежей</div>
      <div>Средний чек: {{payments && (fullPays / payments.length)}} RUB</div>
      <div>Всего: {{fullPays}} RUB</div>
    </v-card-text>
  </v-card>
</v-col>
</template>

<script>
export default {
  props: ['account'],
  data: () => ({
  }),

  computed: {
    fullPays() {
      return this.payments && this.payments.filter(v => v.status !== '1').reduce((acc, v) => acc + parseFloat(v.paymethodamount_iso), 0);
    }
  },

  asyncComputed: {
    payments () {
      return this.api.exec('account.payment', { elid: this.account.elid })
    }
  }
}
</script>
