<template>
  <v-select dense v-if="item.inputtype === 1" v-model="value" :hint="item.hint_ru" :label="item.name_ru" :items="item.itemtypeparamvalue.map(v => v.name_ru)"/>
  <v-text-field dense v-else v-model="value" :label="item.name_ru"/>
</template>

<script>
export default {
  props: ['item', 'container'],

  data: () => ({
    value: ''
  }),

  mounted () {
    this.value = this.defaultvalue;
  },

  watch: {
    value () {
      let value = this.value;
      if (this.item.inputtype === 1) {
        const item = this.item.itemtypeparamvalue.find(v => v.name_ru === value);
        value = item && item.intname;
      }

      if (!value) {
        delete this.container[this.item.intname];
        return;
      }

      
      this.container[this.item.intname] = value;
    }
  }
}
</script>