<template>
<MyDialog title="Зачислить средства">
  <template v-slot:activator="{ on, attrs }">
    <slot name="activator" v-bind="{ attrs, on }"/>
  </template>
<v-card-text>
  <v-text-field v-model="id" label="ID"/>
    <v-text-field autofocus v-model="count" prefix="₽" type="number" label="Количество"/>
    <v-checkbox v-model="checkbox" v-if="count >= 0" label="Отправить счёт на почту"></v-checkbox>
    <v-checkbox v-model="setpaid" v-if="count >= 0" label="Зачислить счёт на баланс"></v-checkbox>
    <v-text-field v-model="name" v-if="count < 0" label="Название"/>
</v-card-text>
<v-card-actions>
    <v-btn @click="payIn" :loading="loading" :disabled="Number.isNaN(parseFloat(count))" color="primary">Зачислить</v-btn>
</v-card-actions>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';
import crypto from 'crypto';

export default {
  components: { MyDialog },
  props: ['userId'],
  data: () => ({
    id: '',
    checkbox: false,
    setpaid: true,
    count: 0,
    loading: false,
    payment: null,
    paymentId: null,
    name: '-'
  }),

  mounted() {
    this.id = this.userId || '';
  },

  methods: {
    async payIn() {
      this.loading = true;
      try {
        if (this.count < 0) {
          const newDate = new Date(Date.now() - new Date().getTimezoneOffset() + 3600e3 * 3);
          const response = await this.api.func('subaccount.expense.edit', {
            plid: this.id,
            // subaccount: 'хз',
            amount: -parseFloat(this.count),
            cdate: `${newDate.getFullYear()}-${newDate.getMonth() + 1}-${newDate.getDate()}`,
            name_ru: this.name,
            name: this.name,
            postpaid: 'off',
            clicked_button: 'ok',
            sok: 'ok'
          });

          this.loading = false;
          if (response.error) {
            alert('Ошибка: ' + response.error.msg);
            return;
          }

          alert(`Расход создан. [${response.doc.plid}]`);
          return;
        }

        this.payment = await this.api.func('payment.add.pay', {
          plid: this.id,
          amount_currency: `${parseFloat(this.count)} RUB`,
          processquickpayment: 'off',
          customer_account: this.id,
          payment_currency: '126',
          paymethod: '7',
          project: '1',
          randomnumber: crypto.randomBytes(4).toString('hex'),
          amount: parseFloat(this.count),
          clicked_button: this.checkbox ? 'finish_adm_send' : 'finish_adm',
          progressid: 'false',
          sok: 'ok'
        });

        // console.log('payment', this.payment)
        this.paymentId = this.payment.payment_id['#text'] || this.payment.payment_id;

        if (!this.setpaid) {
          this.loading = false;
          alert(`Платёж создан, но не зачислен. [${this.paymentId}]`);
          return;
        }

        const response = await this.api.func('payment.setpaid', {
          elid: this.paymentId
        });

        console.log('setpaid', response);
        this.loading = false;
        this.count = 0;
        alert(`Платёж зачислен. [${this.paymentId}]`);
      } catch (error) {
        this.loading = false;
        alert(`Во время зачисления платежа произошла ошибка\n${error.message}`);
        if (this.payment) {
          alert(`Платёж создан, но не зачислен. [${this.paymentId}]`);
        }
      }
    }
  }
};
</script>
