<template>
<MyDialog width="500px" title="Выдать услугу">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item v-bind="attrs" v-on="on">
      <v-list-item-icon>
        <v-icon>mdi-account-arrow-left</v-icon>
      </v-list-item-icon>
      <v-list-item-title>Выдать услугу</v-list-item-title>
    </v-list-item>
  </template>
  <v-card-text>
      <v-select
        v-model="selectedTypeName"
        :items="types ? types.map(v => v.desc) : []"
        :loading="types === null"
        label="Выберите тип услуги"
      />
      <v-autocomplete
        v-model="selectedItemName"
        :items="pricelist ? pricelist.map(v => v.name_ru) : []"
        :loading="pricelist === null"
        label="Выберите услугу"
      />
    <div v-if="selectedItem">
      <div>ID: {{selectedItem.id}}</div>
      <div>Цены: {{api.forceArray(selectedItem.price.period).map(v => `${v['#text']} = ${v.attr['@_cost']}₽`).join(', ')}}</div>
      <h3>Параметры заказа</h3>
      <!--<h4>Общие</h4>-->
      <GiveServiceInput v-for="itemtypeparam in selectedItem.itemtypeparam" :key="itemtypeparam.id" :item="itemtypeparam" :container="params"/>
      <!--<h4>Для этого тарифа</h4>-->
      <!--<GiveServiceInput v-for="addon in selectedItem.addon" :key="addon.id" :item="addon"/>-->
    </div>
    <h3>Дополнительно</h3>
    <v-text-field dense v-model="remoteid" label="Удалённый ID для реселлера"/>
    <v-checkbox dense v-model="payAuto" label="Бесплатно оплатить сервер"/>
    <v-select
      v-model="period"
      :items="[0, -50, 1, 3, 6, 12]"
      label="Период оплаты"
    />
    <!-- <v-checkbox dense label="Установить другую дату окончания"/> -->
    <!-- <v-checkbox dense label="Установить другую цену продления"/> -->
    </v-card-text>
    <v-card-actions>
      <v-btn @click="order" color="primary">Заказать</v-btn>
    </v-card-actions>
</MyDialog>
</template>

<script>
import crypto from 'crypto';
import GiveServiceInput from '@/render/components/Account/GiveServiceInput.vue';
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { GiveServiceInput, MyDialog },
  props: ['account', 'tab'],
  data: () => ({
    data: null,
    params: {},
    period: 1,
    selectedTypeName: 'Виртуальный сервер',
    selectedItemName: '',
    payAuto: false,
    remoteid: ''
  }),

  async mounted () {
    // this.data = await this.api.exec('service.ip', { elid: this.service.elid })
  },

  methods: {
    async order () {
      const type = this.selectedItem.itemtype_info.intname;
      const response = await this.api.func(`${type}.order.param`, {
        su: this.api.emailByName(this.account.name),
        pricelist: this.selectedItem.id,
        datacenter: this.selectedItem.datacenter.id,
        period: this.period,
        sok: 'ok',
        remoteid: this.remoteid,
        ...this.params
      });

      const orderId = response['billorder.id']['#text'] || response['billorder.id'];
      const serverId = response['id']['#text'] || response['id'];
      console.log('Заказ:', response);
      alert(`ID заказа: ${orderId}, ID сервера: ${serverId}, Цена: ${response.itemdesc.main.cost}`);
      this.tabMgr.open('Service', { elId: serverId }, false);

      if (this.payAuto) {
        this.payServer(orderId, serverId, response.itemdesc.main.cost);
      }
    },

    async payServer(orderId, serverId, cost) {
      const payment = await this.api.func('payment.add.pay', {
        billorder: orderId,
        plid: this.account.id,
        amount_currency: `${parseFloat(cost)} RUB`,
        processquickpayment: 'off',
        customer_account: this.account.id,
        payment_currency: '126',
        paymethod: '7',
        project: '1',
        randomnumber: crypto.randomBytes(4).toString('hex'),
        amount: parseFloat(cost),
        clicked_button: 'finish_adm',
        progressid: 'false',
        sok: 'ok'
      }).catch(v => alert(v.message));

      if (!payment) {
        return;
      }

      console.log('payment', payment);
      const paymentId = payment.payment_id['#text'] || payment.payment_id;
      const response = await this.api.func('payment.setpaid', {
          elid: paymentId
        })

      console.log('setpaid', response)
      alert(`Зачислен платёж [${paymentId}]`);
    }
  },

  computed: {
   selectedType() {
     if (!this.types) {
       return 0;
     }

     const selectedTypeFound = this.types.find(v => v.desc === this.selectedTypeName);
     return selectedTypeFound.id;
   },

   pricelist() {
     return this.allPricelist && this.allPricelist.filter(v => v.itemtype === this.selectedType);
   },

   selectedItem() {
     return this.allPricelist && this.allPricelist.find(v => v.name_ru === this.selectedItemName);
   }
  },

  asyncComputed: {
    types () {
      return this.api.array('service.order.itemtype', { su: this.api.emailByName(this.account.name) });
    },

    allPricelist () {
      return this.api.func('pricelist.export', { su: this.api.emailByName(this.account.name), onlyavailable: 'On' }).then(v => v.pricelist);
    }
  }
}
</script>
