<template>
<TabContent v-if="account">
  <AppBar :account="account" :tab="value"/>
  <v-row style="padding: 10px; overflow-y: auto; max-height: 100%">
    <Payments :account="account"/>
    <Tickets :account="account"/>
  </v-row>
  <template v-slot:rightSidebar>
    <Actions :account="account" :tab="value"/>
  </template>
</TabContent>
<TabContentLoading v-else :error="error"/>
</template>

<script>
import TabContent from '@/render/components/Common/TabContent.vue';
import TabContentLoading from '@/render/components/Common/TabContentLoading.vue';
import Actions from '@/render/components/Account/Actions.vue';
import AppBar from '@/render/components/Account/AppBar.vue';
import Payments from '@/render/components/Account/cards/Payments.vue';
import Tickets from '@/render/components/Account/cards/Tickets.vue';

export default {
  components: { TabContent, TabContentLoading, Actions, AppBar, Payments, Tickets },
  props: ['value'],
  data: () => ({
    account: null,
    loading: false,
    error: null
  }),

  async mounted () {
    await this.load();
    this.applyData();
  },

  methods: {
    async load () {
      this.loading = true;
      this.account = await this.tkb.query('get', `billing/service/${this.value.query.elId}/account`);
      this.loading = false;
    },

    async applyData () {
      this.value.title = `A${this.value.query.elId}`;
      this.value.color = 'purple';
      if (!this.account) {
        return;
      }

      this.value.fullTitle = this.account.name;
    }
  }
}
</script>
