<template>
<v-list nav dense>
  <v-list-item @click="platform.openUrl(`https://my.msk.host/billmgr?startform=account&id=${account.id}&filter=on`)">
    <v-list-item-icon>
      <v-icon>mdi-link</v-icon>
    </v-list-item-icon>
    <v-list-item-title>Открыть в панели</v-list-item-title>
  </v-list-item>
  <PayIn :userId="account.id">
    <template v-slot:activator="{ on, attrs }">
      <v-list-item v-bind="attrs" v-on="on">
        <v-list-item-icon>
          <v-icon>mdi-cash-100</v-icon>
        </v-list-item-icon>
        <v-list-item-title>Зачислить средства</v-list-item-title>
      </v-list-item>
    </template>
  </PayIn>
  <GiveService :account="account" :tab="tab"/>
</v-list>
</template>

<script>
import GiveService from '@/render/components/Account/GiveService.vue';
import PayIn from '@/render/components/User/PayIn.vue';

export default {
  components: { GiveService, PayIn },
  props: ['account', 'tab'],
  data: () => ({
  }),

  methods: {
    async openWindow () {
      const goto = await this.api.exec('gotoserver', { elid: this.service.elid }).catch(() => false)
      if (!goto) {
        alert('У этой услуги нет панели управления или она не работает.')
      }

      ipcRenderer.invoke('open-panel-window', goto.ok, this.service.elid)
    }
  }
}
</script>
